  background-position: right 0 top 94px, right 0 top 83px;
  background-repeat: no-repeat;
  position: relative;
}

.header-box__text {
  max-width: 218px;
  color: var(--white);
  font-style: normal;
  font-weight: 400;
  font-family: var(--titan);
  letter-spacing: .04em;
