}

@keyframes firstSlideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes secondSlideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-123%);
  }

  50% {
    opacity: 0;
    transform: translateX(-123%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes thridSlideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-265%);
  }

  67% {
    opacity: 0;
    transform: translateX(-265%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.logo {
  animation: .4s cubic-bezier(.9, .17, .83, .67) firstSlideInFromLeft;
}

.header-box__text {
  animation: .8s cubic-bezier(.9, .17, .83, .67) secondSlideInFromLeft;
}

.btn-box__red, .btn-box__white {
  animation: 1.2s cubic-bezier(.9, .17, .83, .67) thridSlideInFromLeft;
